import * as React from 'react';

import { getCN } from '@miq/utiljs';
import { Button, ButtonProps, ConfirmButton } from '@miq/componentjs';

import { TSettingInstance } from './models';

export const SitePublishButton: React.FC<
  ButtonProps & {
    instance: TSettingInstance;
    onSuccess?: (r: any) => void;
  }
> = (props) => {
  const { instance, onClick, onSuccess, onError, ...rest } = props;
  const { is_live = false } = instance;

  const handleClick = (setOpen: any) => {
    return instance
      .update({ is_live: !is_live })
      .then((res) => {
        setOpen(false);
        onSuccess?.(res);
      })
      .catch((err) => onError?.(err));
  };

  return (
    <ConfirmButton
      renderHeader={() => <div>{is_live ? 'Unpublish' : 'Publish'} this site</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Cancel</Button>

          <Button onClick={() => handleClick(setOpen)} className={is_live ? 'btn-danger' : 'btn-primary'}>
            {is_live ? 'Unpublish' : 'Publish'}
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">{`Please confirm you want to ${is_live ? 'unpublish' : 'publish'} this site`}</div>
      )}
      className={getCN([is_live ? 'btn-danger-3' : 'btn-primary'])}
    >
      {is_live ? 'Unpublish site' : 'Publish site'}
    </ConfirmButton>
  );
};
