import * as React from 'react';
import ReactDOMServer from 'react-dom/server';

import { getCN } from '@miq/utiljs';
import Form, { useForm } from '@miq/formjs';
import { Button, Img, Loading } from '@miq/componentjs';

import { StaffImg } from '../models';
import { StaffSection } from '../components';
import { StaffImgAddButton, StaffImgDeleteButton, StaffImgUpdateButton } from '../img';

import { SitePublishButton } from './components';
import { TSettingInstance, TSetting } from './models';

interface IProps {
  instance: TSettingInstance;
  onUpdate: (payload: any) => void;
}

export default function SitePrivacyUpdateSection(props: IProps) {
  const { instance, onUpdate } = props;
  return (
    <StaffSection
      title="Site Privacy"
      actions={<SitePublishButton instance={instance} onSuccess={({ data }) => onUpdate(data)} />}
    >
      {!instance.is_live && (
        <div className="d-grid grid-md-2 gap-3">
          <CloseTemplateForm {...props} />
          <CloseTemplateCoverUpdateForm {...props} />
        </div>
      )}
    </StaffSection>
  );
}

const CloseTemplateForm = (props: IProps & {}) => {
  const { instance, onUpdate } = props;
  const form = useForm({ ct_title: instance.ct_title || '', ct_text: instance.ct_text || '' });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    return instance
      .update({ ...form.values, ct_html: getHtml({ ...instance, ...form.values }) })
      .then(({ data }: any) => {
        onUpdate(data);
        // props.toast.success.({ message: 'Template updated.' });
      })
      .catch((err: any) => form.handleError(err));
  };

  return (
    <Form context={form} onSubmit={handleSubmit}>
      <Form.Field label="Title">
        <Form.Text name="ct_title" placeholder="Write title ..." error={form.errors.ct_title} maxLength={50} />
      </Form.Field>

      <Form.Field label="Text" className="mb-2">
        <Form.TextAreaX name="ct_text" placeholder="Write text ..." error={form.errors.ct_text} maxLength={99} />
      </Form.Field>

      <div>
        <Form.Submit value="Save" className="btn btn-primary-2" />
      </div>
    </Form>
  );
};

const CloseTemplateCoverUpdateForm = (props: IProps) => {
  const { onUpdate, instance } = props;
  const [view, setView] = React.useState<'src' | 'mobile' | 'picture'>('src');

  if (!instance.ct_image)
    return (
      <div className="d-flex align-items-center justify-content-center flex-column text-center border-1 rounded p-3">
        <StaffImgAddButton
          onSuccess={({ data }: any) => {
            const newData: TSetting = { ...instance, ct_image: data.slug, ct_image_data: data };
            instance
              .update({ ct_image: data.slug, ct_html: getHtml(newData) })
              .then(({ data }: any) => {
                onUpdate(data);
                // toast.success.({ message: 'Image added.' });
              })
              .catch((err: any) => {});
          }}
          label="Ajouter une image"
          className="btn-primary-3 btn-md"
        />
      </div>
    );

  const coverInstance = StaffImg(instance.ct_image_data);

  const Wrapper = ({ children }: any) => (
    <StaffSection
      border
      title={
        <StaffImgDeleteButton
          {...props}
          instance={coverInstance}
          onSuccess={() => {
            const newData: TSetting = { ...instance, ct_image: undefined, ct_image_data: undefined };
            instance
              .update({ ct_html: getHtml(newData) })
              .then(({ data }: any) => {
                onUpdate(data);
                // toast.success.({ message: 'Image deleted.' });
              })
              .catch((err: any) => {});
          }}
        />
      }
      actions={
        <div className="d-flex align-items-center">
          <Button onClick={() => setView('src')} className={getCN([view === 'src' && 'btn-primary-3', 'me-1'])}>
            Desktop
          </Button>
          <Button onClick={() => setView('mobile')} className={getCN([view === 'mobile' && 'btn-primary-3', 'me-1'])}>
            Mobile
          </Button>
          <Button onClick={() => setView('picture')} className={getCN([view === 'picture' && 'btn-primary-3'])}>
            Picture
          </Button>
        </div>
      }
    >
      <StaffImgUpdateButton
        instance={coverInstance}
        field={view}
        onSuccess={({ data }: any) => {
          const newData: TSetting = { ...instance, ct_image: data.slug, ct_image_data: data };
          instance
            .update({ ct_html: getHtml(newData) })
            .then(({ data }: any) => {
              onUpdate(data);
              // toast.success.({ message: 'Image updated.' });
            })
            .catch((err: any) => {});
        }}
      >
        {children}
      </StaffImgUpdateButton>
    </StaffSection>
  );

  switch (view) {
    case 'mobile':
      return (
        <Wrapper>
          <StaffImg.Mobile {...coverInstance.export()} />
        </Wrapper>
      );

    case 'picture':
      return (
        <Wrapper>
          <StaffImg.Picture {...coverInstance.export()} />
        </Wrapper>
      );

    default:
      return (
        <Wrapper>
          <StaffImg.Image {...coverInstance.export()} />
        </Wrapper>
      );
  }
};

const CloseTemplateRender = ({ ct_title, ct_text, ct_image_data, ...setting }: TSetting) => {
  const hasText = ct_title || ct_text;
  const { logo_data, logo } = setting || {};

  return (
    <div className="close-template">
      {ct_image_data && <Img.Picture {...ct_image_data} className="close-template-bg" />}

      {hasText && (
        <div className="close-template-inner">
          <div className={getCN(['close-template-logo', !logo && 'loading'])}>
            {logo_data ? <Img.Square {...logo_data} /> : <Loading icon />}
          </div>
          <div className="text-center p-3">
            {ct_title && <h1 className="close-template-title mb-1">{ct_title}</h1>}
            {ct_text && <h3 className="close-template-text">{ct_text}</h3>}
          </div>
        </div>
      )}
    </div>
  );
};

const getHtml = (data: TSetting) => ReactDOMServer.renderToStaticMarkup(<CloseTemplateRender {...{ ...data }} />);
